@charset "UTF-8";

//----------------------------------------------
.section-range {
  padding-top: 69px;
  padding-bottom: 150px;
  overflow: hidden;

  @media (max-width: $screen-xs-max) {
    padding-top: 15vw;
    padding-bottom: 50px;
  }

  .title {
    margin-top: 0;
    margin-bottom: 70px;
    font-size: 50px;
    text-align: center;

    @media (max-width: $screen-xs-max) {
      margin-bottom: 14vw;
      font-size: 9vw;
    }
  }

  .container-input {
    @include center-block ();
    margin-top: 80px;
    margin-bottom: 80px;

    @media (max-width: $screen-xs-max) {
      margin-top: 40px;
      margin-bottom: 50px;
    }

    .form-inline {
      display: flex;

      .form-group {
        width: 100%;
        margin-right: 20px;
        margin-bottom: 0;

        @media (max-width: $screen-xs-max) {
          margin-right: 10px;
        }
      }
    }

    .form-control {
      width: 100%;
      height: 50px;
      padding: 10px 30px;
      font-size: 25px;
      text-align: center;
      border-radius: 25px;

      @media (max-width: $screen-xs-max) {
        padding-right: 20px;
        padding-left: 20px;
        font-size: 16px;
      }
    }

    .btn {
      width: 56px;
      height: 50px;
      padding: 12px 15px;
      border-radius: 18px;

      @media (max-width: $screen-xs-max) {
        width: 67px;
        height: 52px;
        padding: 13px 5px 11px;
        border-radius: 26px;
      }

      svg {
        @media (max-width: $screen-xs-max) {
          width: 8vw;
        }
      }

      &:hover,
      &:focus {
        background-color: #000;
        border-color: #000;

        svg {
          path {
            fill: #fff;
          }
        }
      }
    }
  }
}

//----------------------------------------------
.range-slider__dots {
  display: flex;
  justify-content: space-between;
  width: 100%;
  position: absolute;
  top: -5px;
  left: 0;
  z-index: -1;

  span {
    display: block;
    width: 18px;
    height: 18px;
    background-color: #d7dcdf;
    border-radius: 50%;
  }
}
