@charset "UTF-8";

//----------------------------------------------
// Base Colors
$shade-10: #6a6bb6 !default;
$shade-1: #d7dcdf !default;
$shade-0: #fff !default;
$teal: #1abc9c !default;


// Reset
* {
  &,
  &:before,
  &:after {
    box-sizing: border-box;
  }
}

.range-slider {
  position: relative;
  margin-bottom: 100px;

  @media (max-width: $screen-xs-max) {
    margin-bottom: 18vw;
  }

  &.character-range {
    .bubble {
      display: none;
    }
  }
}


// Range Slider
$range-width: 100% !default;

$range-handle-color: $shade-10 !default;
$range-handle-color-hover: $teal !default;
$range-handle-size: 30px !default;

$range-track-color: $shade-1 !default;
$range-track-height: 8px !default;

$range-label-color: $shade-10 !default;
$range-label-width: 100px !default;

.range-slider {
  display: flex;
  align-items: center;
  width: $range-width;
}

.range-slider__range {
  -webkit-appearance: none;
  width: calc(100% - (#{$range-label-width + 13px}));
  height: $range-track-height;
  border-radius: 5px;
  background: $range-track-color;
  outline: none;
  padding: 0;
  margin: 0;

  // Range Handle
  &::-webkit-slider-thumb {
    appearance: none;
    width: $range-handle-size;
    height: $range-handle-size;
    border-radius: 50%;
    background: $range-handle-color;
    cursor: pointer;
    transition: background .15s ease-in-out;

    &:hover {
      background: $range-handle-color-hover;
    }
  }

  &:active::-webkit-slider-thumb {
    background: $range-handle-color-hover;
  }

  &::-moz-range-thumb {
    width: $range-handle-size;
    height: $range-handle-size;
    border: 0;
    border-radius: 50%;
    background: $range-handle-color;
    cursor: pointer;
    transition: background .15s ease-in-out;

    &:hover {
      background: $range-handle-color-hover;
    }
  }

  &:active::-moz-range-thumb {
    background: $range-handle-color-hover;
  }

  // Focus state
  &:focus {

    &::-webkit-slider-thumb {
      box-shadow: 0 0 0 3px $shade-0,
      0 0 0 6px $teal;
    }
  }
}


// Range Label
.range-slider__value {
  display: inline-block;
  position: relative;
  width: $range-label-width;
  color: $shade-0;
  line-height: 20px;
  text-align: center;
  border-radius: 3px;
  background: $range-label-color;
  padding: 5px 10px;
  margin-left: 15px;

  &.range-input {
    padding: 0;
    background-color: transparent;

    .form-control {
      font-size: 16px;
      border-radius: 18px;
      text-align: center;
    }

    &:after {
      display: none;
    }
  }

  &:after {
    position: absolute;
    top: 8px;
    left: -7px;
    width: 0;
    height: 0;
    border-top: 7px solid transparent;
    border-right: 7px solid $range-label-color;
    border-bottom: 7px solid transparent;
    content: '';
  }
}


// Firefox Overrides
::-moz-range-track {
  background: $range-track-color;
  border: 0;
}

input::-moz-focus-inner,
input::-moz-focus-outer {
  border: 0;
}

.bubble {
  position: absolute;
  top: 45px;
  left: 50%;
  min-width: 92px;
  padding: 4px 25px;
  font-size: 16px;
  text-align: center;
  color: #000;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #000;

  @media (max-width: $screen-xs-max) {
    font-size: 5vw;
  }
}

//----------------------------------------------
.complexity-range {
  position: relative;
  margin-bottom: 20px;

  .range-slider__range {
    &::-webkit-slider-thumb {
      position: relative;
      top: 0;
      z-index: 1;
    }
  }
}

//----------------------------------------------
.data-list-password {
  display: flex;

  @media (max-width: 1300px) {
    display: none;
  }

  option {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 19.9%;
    font-size: 16px;
    font-weight: bold;

/*    &:first-child {
      text-align: left;
    }

    &:last-child {
      text-align: right;
    }*/
  }
}

//----------------------------------------------
.bubble-complexity-range {
  position: static;
  display: inline-block;
  margin-bottom: 20px;
}
