@charset "UTF-8";

//----------------------------------------------
@mixin media-cleared() {
  &.media,
  .media-body {
    overflow: visible;
  }

  .media-body {
    width: auto;
  }

  .media-left,
  .media-right,
  .media-body {
    display: block;
  }

  .media-right,
  .media > .pull-right {
    padding-bottom: 10px;
    padding-left: 0 !important;
  }

  .media-left,
  .media > .pull-left {
    padding-right: 0 !important;
    padding-bottom: 10px;
  }

  .media-object {
    display: inline-block;
  }
}

//----------------------------------------------
.media {
  &.media-cleared-xs {
    @media (max-width: $screen-xs-max) {
      @include media-cleared();
    }
  }

  &.media-cleared-sm {
    @media (max-width: $screen-sm-max) {
      @include media-cleared();
    }
  }

  &.media-direction-rtl {
    @media (min-width: $screen-sm-min) {
      // from right
      direction: rtl;

      .media-body,
      .media-left {
        direction: ltr;
      }

      .media-right {
        padding-right: 10px;
        padding-left: 0;
        direction: ltr;
      }
    }
  }
}

//----------------------------------------------
.media-elastic-thumbnail {
  margin-bottom: 15px;
  background-color: #fff;

  .media-left {
    min-width: 130px;
    padding-bottom: 36%;

    @media (max-width: $screen-xs-max) {
      min-width: 75px;
      padding-bottom: 30%;
    }

    &.img-1 {
      background: url("../img/media/img-1.png") no-repeat 50% 50%/cover;
    }
  }

  .media-body {
    padding-left: 15px;
  }
}

//----------------------------------------------
.media-bordered-blue {
  background-color: transparent;
  border: 5px solid #355ea6;
  border-radius: 10px;

  .media-left {
    min-width: 200px;
    padding-bottom: 15%;
  }

  .media-body {
    padding: 15px;
  }

  .img-cleaning-2 {
    background: url("../img/media/img-1.png") 50% 50% no-repeat;
    background-size: cover;
  }
}

//----------------------------------------------
.media-separated {
  border-top: 1px solid #000;

  .media-left,
  .media-body {
    padding-top: 20px;

    @media (max-width: $screen-xs-max) {
      padding-top: 10px;
    }
  }

  .media-left {
    min-width: 174px;
    font-weight: bold;
    border-right: 1px solid #000;

    @media (min-width: $screen-sm-min) {
      font-size: 18px;
    }

    @media (max-width: $screen-xs-max) {
      min-width: 100px;
    }
  }

  .media-body {
    padding-left: 26px;

    @media (max-width: $screen-xs-max) {
      padding-left: 15px;
    }
  }
}

//----------------------------------------------
.media-styled {
  padding: 10px;

  .media-left {
    padding: 10px;
    color: #fff;
    text-align: center;

    @media (min-width: $screen-sm-min) {
      // height: 160px;
      min-width: 240px;
      font-size: 20px;
    }
  }

  &.gray {
    background-color: #eee;

    .media-left {
      background-color: #999;
    }
  }

  &.media-cleared-xs {
    .media-left {
      @media (max-width: $screen-xs-max) {
        padding-right: 10px !important;
      }
    }
  }

  .media-body {
    @media (min-width: $screen-sm-min) {
      padding-left: 10px;
    }

    @media (max-width: $screen-xs-max) {
      padding-top: 10px;
    }
  }

  ul {
    margin-bottom: 0;
  }
}

//----------------------------------------------
.media-img-parallax {
  .media-left {
    position: relative;
    min-width: 770px;
    height: 900px;
    padding-right: 0;

    @media (min-width: $screen-md-min) and (max-width: $screen-md-max) {
      min-width: 45vw;
    }

    @media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
      min-width: 40vw;
      height: 600px;
    }

    @media (max-width: $screen-xs-max) {
      min-width: 100%;
      height: 400px;
    }
  }

  .media-body {
    padding-right: 150px;

    @media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
      padding-right: 120px;
    }

    @media (max-width: $screen-xs-max) {
      padding-top: 35px;
      padding-right: 0;
    }
  }

  .img-parallax {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    overflow: hidden;

    .bg {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }

    &.img-1 {
      left: -100px;
      z-index: 2;
      width: 580px;
      height: 800px;

      @media (min-width: $screen-md-min) and (max-width: $screen-md-max) {
        left: -12vw;
        width: 38vw;
      }

      @media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
        width: 41vw;
        height: 80vw;
      }

      @media (max-width: 990px) and (min-width: 768px) {
        top: 74%;
      }

      @media (max-width: $screen-xs-max) {
        top: 50px;
        left: 0;
        width: 60vw;
        height: 350px;
      }

      .bg {
        background: url("../img/common/building-1.jpg") 50% 50% no-repeat;
        background-size: cover;
      }
    }

    &.img-2 {
      width: 500px;
      height: 720px;

      @media (min-width: $screen-md-min) and (max-width: $screen-md-max) {
        width: 32vw;
      }

      @media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
        width: 35vw;
        height: 64vw;
      }

      @media (max-width: $screen-xs-max) {
        width: 58vw;
        height: 300px;
      }

      .bg {
        background: url("../img/common/building-2.jpg") 50% 50% no-repeat;
        background-size: cover;
      }
    }
  }
}
