@charset "UTF-8";

//----------------------------------------------
.navbar-default {
  background-color: #fff;
  border-bottom: 0;

  .navbar-right {
    @media (max-width: $screen-xs-max) {
      padding-right: 15px;
    }
  }
}

//----------------------------------------------
.js-header {
  &.is-scrolled {
    position: fixed;
    top: -200px;
  }

  &.is-transition {
    @include transition(top .3s ease-in-out);
    will-change: top;
  }

  &.is-show {
    top: 0;
  }
}

//----------------------------------------------
.nav-adjoined-bordered {
  margin-bottom: 15px;
  background-color: #ccc;

  ul {
    @include clearfix;
    padding-left: 0;
    margin-top: 10px;
    margin-bottom: 10px;
    text-align: center;
    list-style: none;
    border-right: 1px solid #000;
    box-shadow: 1px 0 0 0 #fff;

    li {
      position: relative;
      float: left;
      width: 50%;

      @media (min-width: $screen-sm-min) {
        width: 33.33333333%;
      }

      @media (min-width: $screen-md-min) {
        width: 16.66666666666667%;
      }

      a {
        display: block;
        padding-top: 5px;
        padding-bottom: 5px;
        line-height: 1.1;
        border-left: 1px solid #fff;
        box-shadow: -1px 0 0 0 #000;

        &:before,
        &:after {
          position: absolute;
          top: 0;
          bottom: 0;
          content: "";
        }

        &:before {
          left: 0;
        }

        &:after {
          right: 0;
        }

        &:hover {
          @include opacity(.75);
          text-decoration: none;
        }

        span {
          display: block;
          font-size: 10px;
        }
      }
    }
  }
}

//----------------------------------------------
.menu-trigger {
  position: absolute;
  top: 3px;
  right: 0;
  z-index: 1000;
  width: 40px;
  height: 32px;
  transition: all .3s;

  @media (max-width: $grid-float-breakpoint-max) {
    display: inline-block;
  }

  &,
  &:hover,
  &:focus,
  &:active {
    background-color: transparent !important;
    border: 0;
    outline: 0;
  }

  .icon-bar {
    position: absolute;
    left: 0;
    display: inline-block;
    width: 100%;
    height: 4px;
    background-color: #888;
    border-radius: 4px;
    transition: all .3s;

    &:nth-of-type(1) {
      top: 0;
    }

    &:nth-of-type(2) {
      top: 14px;
    }

    &:nth-of-type(3) {
      bottom: 0;
    }

    & + .icon-bar {
      margin-top: 0;
    }
  }

  &.active {
    .icon-bar {
      &:nth-of-type(1) {
        transform: translateY(14px) rotate(-45deg);
      }

      &:nth-of-type(2) {
        opacity: 0;
      }

      &:nth-of-type(3) {
        transform: translateY(-14px) rotate(45deg);
      }
    }
  }
}

//----------------------------------------------
.collapse-parent {
  $space: 6px;

  position: relative;

  .collapse-child {
    @include box-shadow(0 6px 12px rgba(0, 0, 0, .175));
    background-color: #fff;

    @media (min-width: $grid-float-breakpoint) {
      position: absolute;
      top: 100%;
      right: 0;
      left: 0;
      z-index: $zindex-dropdown;
    }

    @media (max-width: $grid-float-breakpoint-max) {
      @include box-shadow(0 3px 6px rgba(0, 0, 0, .175));
    }

    ul {
      padding-top: $space;
      padding-bottom: $space;
    }
  }

  a {
    display: block;
    padding: $space 15px;
  }

  .collapsing {
    transition: height .2s ease;
  }

  [data-toggle-touch] {
    position: absolute;
    top: 0;
    right: 0;
  }
}

//----------------------------------------------
.nav-tabs-table-cell {
  display: table;
  width: 640px;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  border: 0;

  @media (max-width: $screen-xs-max) {
    width: auto;
  }

  > li {
    display: table-cell;
    padding-right: 5px;
    padding-left: 5px;
    vertical-align: middle;

    > a {
      display: block;
      padding-right: 10px;
      padding-left: 10px;
      margin-right: 0;
      color: #008cc9;
      border: 1px solid #008cc9;
      border-radius: 0;

      &:hover,
      &:focus {
        color: #fff;
        background-color: #008cc9;
      }
    }

    &.active {
      > a {
        position: relative;
        color: #fff;
        background-color: #008cc9;
        border: 1px solid #008cc9;

        &:hover,
        &:focus {
          color: #fff;
          background-color: #008cc9;
        }
      }
    }
  }
}

//----------------------------------------------
.scrollspy-nav {
  position: fixed;
  top: 0;
  right: -100px;
  bottom: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: auto;
  margin-bottom: auto;
  font-weight: 300;
  transition: all .15s ease;

  &.active {
    right: 20px;
  }

  .nav {
    padding: 0;
    margin: 0;

    > li {
      width: 75px;
      padding-top: 15px;
      padding-bottom: 15px;

      &.active {
        a {
          background-color: transparent;

          &:before {
            border-color: #b38700;
          }

          &:after {
            background-color: #b38700;
          }
        }

        .text {
          @include opacity(1);
        }
      }
    }
  }

  a {
    position: relative;
    display: block;
    width: 100%;
    height: 30px;
    // padding-right: 32px;
    font-size: 12px;
    color: #231815;
    // background-color: red;
    transition: all .15s ease;

    &:before,
    &:after {
      position: absolute;
      content: "";
      transition: all .15s ease;
    }

    &:before {
      top: 7px;
      right: -4px;
      width: 15px;
      height: 15px;
      border: 2px solid transparent;
      border-radius: 50%;
    }

    &:after {
      top: 11px;
      right: 0;
      width: 7px;
      height: 7px;
      border-radius: 50%;
      background-color: #231815;
    }

    &:hover,
    &:focus {
      background-color: transparent !important;
      text-decoration: none;
    }

    &:hover {
      &:before {
        border-color: #b38700;
      }

      &:after {
        background-color: #b38700;
      }

      .text {
        @include opacity(1);
      }
    }
  }

  .text {
    @include opacity (0);
    position: absolute;
    top: 5px;
    left: -14px;
    display: inline-block;
    white-space: nowrap;
    letter-spacing: .09em;
    transition: all .15s ease;
    text-shadow: 1px 1px 0 rgb(255, 255, 255);
  }
}

//----------------------------------------------
.navigation {
  @extend .list-unstyled;
  margin-bottom: 0;
  overflow: hidden;

  li {
    display: block;

    a {
      display: block;
      padding: 0 10px 100%;
      overflow: hidden;

      &:hover {
        .img {
          width: 100%;
        }
      }
    }
  }

  .img {
    @include transition(all .3s ease);
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: block;
    width: 204px;
    margin: auto;

    @media (max-width: $screen-xs-max) {
      width: 60%;
    }
  }

  .title {
    position: absolute;
    top: 10%;
    left: 0;
    right: 0;
    z-index: 1;
    margin-top: 0;
    margin-bottom: 0;
  }

  .caption {
    position: absolute;
    left: 0;
    bottom: 10%;
    right: 0;
    z-index: 1;
    display: block;
    margin-top: 19px;
    font-size: 20px;

    @media (max-width: $screen-sm-max) {
      font-size: 18px;
    }

    @media (max-width: $screen-xs-max) {
      bottom: 6%;
      font-size: 2.67vw;
    }
  }
}
