.range-slider__range {
  -webkit-appearance: none;
  width: calc(100% - (113px));
  height: 8px;
  border-radius: 5px;
  background: #d7dcdf;
  outline: none;
  padding: 0;
  margin: 0;
}
.range-slider__range::-webkit-slider-thumb {
  appearance: none;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #6a6bb6;
  cursor: pointer;
  transition: background 0.15s ease-in-out;
}
